import Vue from 'vue'
import Layout from './Layout.vue'
import router from './router'
import VAnimateCss from 'v-animate-css'
import './assets/scss/custom-styles.scss'
import './fortawesome-icons.js'

Vue.use(VAnimateCss)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(Layout)
}).$mount('#app')
